  /* Name */ "n": #abb3be,
  /* NameAttribute */ "na": #5995ED,
  /* NameBuiltin */ "nb": #19b9c4,
  /* NameBuiltinPseudo */ "bp": #ecbf6f,
  /* NameClass */ "nc": #ecbf6f,
  /* NameConstant */ "no": #5995ED,
  /* NameDecorator */ "nd": #ecbf6f,
