  /* NameVariableGlobal */ "vg": #5995ED,
  /* NameVariableInstance */ "vi": #5995ED,
  /* NameVariableMagic */ "vm": #5995ED,
  /* Literal */ "l": #34D1BF,
  /* LiteralDate */ "ld": #34D1BF,
  /* LiteralString */ "s": #99c279,
  /* LiteralStringAffix */ "sa": #99c279,
  /* LiteralStringBacktick */ "sb": #99c279,
  /* LiteralStringChar */ "sc": #99c279,
  /* LiteralStringDelimiter */ "dl": #99c279,
  /* LiteralStringDoc */ "sd": #99c279,
  /* LiteralStringDouble */ "s2": #99c279,
  /* LiteralStringEscape */ "se": #99c279,
  /* LiteralStringHeredoc */ "sh": #99c279,
  /* LiteralStringInterpol */ "si": #99c279,
  /* LiteralStringOther */ "sx": #99c279,
  /* LiteralStringRegex */ "sr": #99c279,
  /* LiteralStringSingle */ "s1": #99c279,
  /* LiteralStringSymbol */ "ss": #99c279,
  /* LiteralNumber */ "m": #e5c07b,
  /* LiteralNumberBin */ "mb": #e5c07b,
